.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.person{
  background-color: #30353d;
  color: white;
}
.person{
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items:space-between;
  justify-content: center;

}
.leftCont{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin: 20px;
  padding: 1%;
  color: rgb(5, 210, 237);
  width: 60vw;
  height: 75vh;
}

li{
  list-style: none;
}
.para{
  font-size: 20px;
  color: white;
  margin: 20px 0px;
  text-align: left;
}
.buttons{
  display: flex;
}
.btn{
  background-color: white;
  color: #282c34;
  margin: 0px 10px;
 
}
.rightCont{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin: 10px;
  padding: 1%;
  color: rgb(253, 253, 253);
  width: 40vw;
  height: 75vh;

}
.student{
  text-align: center;
  background-color: #949494;
  color: whitesmoke;
  margin-top: 20px  ;
  padding: 10px;
}

.toggle-main{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.form{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
}
.form-text{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
}
.login{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
}
.prop-text{
  text-align: center;
}
input{
  border-radius: 6px;
  width: auto;
  line-height: 1px;
}

.btns{
  border-radius: 6px;
  background-color:rgb(9, 9, 125) ;
  color: white;
  width: 100px;
  height: 35px;
  border-color: white;
}
.btns:hover{
  border-radius: 6px;
  background-color:rgb(255, 255, 255) ;
  color: rgb(9, 9, 125);
  width: 102px;
  height: 36px;
}
.nd{
  margin:20px
}